.form {
  max-width: 1440px;

  @media (min-width: $tablet-width) {
    margin-top: 53px;
    margin-bottom: 62px;
  }
}

.form__description {
  font-family: $oswald;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
  color: $black;
  width: 70.63vw;
  margin: 0;
  padding-bottom: 39px;
  padding-left: 6%;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 30px;
    width: 42.32vw;
    padding-left: 9.3%;
  }

  @media (min-width: $desktop-width) {
    padding-left: 110px;
  }
}

.form__label {
  font-family: $oswald;
  font-size: 16px;
  line-height: 18px;
  color: $black-two;
  text-transform: uppercase;
  padding-right: 15px;
  white-space: nowrap;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 20px;
  }

  &--radio {
    position: relative;
    padding-left: 46px;
    padding-top: 8px;

    @media (min-width: $tablet-width) {
      padding-left: 60px;
    }

    &::before {
      top: 1px;
      left: 1px;
      width: 30px;
      height: 30px;
      border: 1px solid $grey-form;
      border-radius: 50%;
      position: absolute;
      box-shadow: inset 0 0 0 9px #ffffff;
      content: "";

      @media (min-width: $tablet-width) {
        width: 40px;
        height: 40px;
        box-shadow: inset 0 0 0 11px #ffffff;
      }
    }
  }

  &--checkbox {
    position: relative;
    padding-left: 21%;
    padding-top: 5px;
    padding-bottom: 7px;

    @media (min-width: $tablet-width) {
      padding-left: 129px;
    }

    @media (min-width: $desktop-width) {
      padding-left: 65px;
    }

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 6vw;
      width: 30px;
      height: 30px;
      border: 1px solid $grey-form;

      @media (min-width: $tablet-width) {
        width: 40px;
        height: 40px;
        left: 70px;
      }

      @media (min-width: $desktop-width) {
        left: 0;
      }
    }
  }
}

.form__field {
  margin: 0;

  &--text {
    display: flex;
    align-items: center;
    padding-bottom: 14px;
    padding-left: 6%;
    padding-right: 20px;

    @media (min-width: $tablet-width) {
      padding-left: 9.3%;
      padding-right: 67px;
      padding-bottom: 20px;
    }

    @media (min-width: $desktop-width) {
      width: 570px;
      padding-left: 110px;
      padding-right: 80px;
    }
  }

  &--radio {
    padding-top: 27px;
    padding-left: 6%;

    @media (min-width: $tablet-width) {
      box-sizing: border-box;
      width: 630px;
      margin: 0 auto;
      padding-left: 28px;
    }
  }

  &--tel {
    padding-bottom: 31px;

    @media (min-width: $desktop-width) {
      padding-left: 0;
    }
  }

  &--additional {
    margin-bottom: 28px;

    @media (min-width: $tablet-width) {
      width: 257px;
      padding-right: 86px;
      margin-bottom: 50px;
    }

    @media (min-width: $desktop-width) {
      padding-right: 0;
      width: 324px;
    }
  }
}

.form__info-container {
  @media (min-width: $tablet-width) {
    display: flex;
    flex-direction: column;
  }

  @media (min-width: $desktop-width) {
    flex-direction: row;
  }
}

.form__target-container {
  border-top: 1px solid #ededed;
  border-bottom: 1px solid #ededed;
  padding-bottom: 22px;
  margin-bottom: 31px;

  @media (min-width: $tablet-width) {
    width: 630px;
    border: 1px solid #ededed;
    align-self: center;
  }

  @media (min-width: $desktop-width) {
    align-self: baseline;
    margin-bottom: 0;
    min-height: 190px;
    box-sizing: border-box;
    width: 570px;
  }
}

.form__contacts-container {
  @media (min-width: $desktop-width) {
    display: flex;
  }
}

.form__input {
  &--text {
    font-family: $oswald;
    font-size: 16px;
    line-height: 18px;
    color: $greyish;
    text-transform: uppercase;
    width: 100%;
    padding-left: 16px;
    min-height: 45px;
    border: 1px solid #cdcdcd;

    @media (min-width: $tablet-width) {
      font-size: 20px;
      line-height: 20px;
    }
  }
}

.form__input-radio {
  &:checked + .form__label--radio::before {
    background-color: $green;
  }
}

.form__input-checkbox {
  &:checked + .form__label--checkbox::before {
    background-image: url("../img/icon-tick.svg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: 22px 17px;
  }
}

.form__title {
  font-family: $oswald;
  font-size: 24px;
  font-weight: 400;
  line-height: 24px;
  color: $black;
  box-sizing: border-box;
  width: 63vw;
  margin: 0;
  padding-bottom: 30px;
  padding-left: 6%;
  position: relative;

  @media (min-width: $tablet-width) {
    font-size: 36px;
    line-height: 36px;
    width: 76.83vw;
    padding-left: 9.3%;

    &::before {
      position: absolute;
      top: 19px;
      left: 0;
      width: 3.52vw;
      height: 3px;
      background-color: $green;
      content: "";

      @media (min-width: $desktop-width) {
        width: 72px;
      }
    }
  }

  @media (min-width: $desktop-width) {
    padding-left: 110px;
  }

  &::after {
    content: "";
    height: 3px;
    background-color: $green;
    position: absolute;
    top: 15px;
    left: 221px;

    @media (min-width: $tablet-width) {
      top: 22px;
    }
  }

  &--contacts {
    &::after {
      width: 30.94vw;
      left: 69vw;

      @media (min-width: $tablet-width) {
        width: 19.02vw;
        left: 81vw;
      }

      @media (min-width: $desktop-width) {
        width: 775px;
        left: 665px;
      }
    }
  }

  &--comments {
    &::after {
      width: 49.38vw;
      left: 50.5vw;

      @media (min-width: $tablet-width) {
        width: 62.5vw;
        left: 37.5vw;
      }

      @media (min-width: $desktop-width) {
        width: 1111px;
        left: 329px;
      }
    }
  }

  &--additional {
    padding-bottom: 39px;

    @media (min-width: $tablet-width) {
      padding-bottom: 52px;
    }

    &::after {
      width: 42.5vw;
      left: 57.4vw;

      @media (min-width: $tablet-width) {
        width: 58.21vw;
        left: 41.8vw;
      }

      @media (min-width: $desktop-width) {
        width: 1076px;
        left: 364px;
      }
    }
  }
}


.form__comment {
  font-family: $oswald;
  font-size: 16px;
  line-height: 18px;
  color: $greyish;
  text-transform: uppercase;
  box-sizing: border-box;
  border: 1px solid $grey-form;
  width: 87.5vw;
  min-height: 150px;
  resize: none;
  margin-left: 6%;
  margin-bottom: 31px;
  padding-left: 16px;
  padding-top: 15px;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 30px;
    width: 82.17vw;
    margin-left: 9.3%;
  }

  @media (min-width: $desktop-width) {
    width: 1220px;
    min-height: 174px;
    margin-left: 110px;
  }
}

.form__legend {
  font-family: $arial;
  font-size: 14px;
  line-height: 16px;
  color: $greyish;
  display: flex;
  justify-content: center;
  margin: 0;
  padding-top: 23px;

  @media (min-width: $tablet-width) {
    font-size: 16px;
    padding-left: 61px;
  }

  @media (min-width: $desktop-width) {
    padding-left: 82px;
  }
}

.form__additional-container {
  @media (min-width: $tablet-width) {
    display: flex;
    flex-wrap: wrap;
    padding-top: 12px;
  }

  @media (min-width: $desktop-width) {
    width: 1300px;
    margin-left: 110px;
  }
}

.form__submit-container {
  @media (min-width: $tablet-width) {
    display: flex;
    margin-left: 70px;
  }

  @media (min-width: $desktop-width) {
    margin-left: 110px;
  }
}
